import React, { useState, useEffect } from "react";
import "./NewsSection.css"; // Assurez-vous d'avoir un fichier CSS pour les styles

const NewsSection = () => {
  const newsData = [
    {
      title: "Dcouverte d'une nouvelle exoplante dans la zone habitable.",
      image: "/images/exoplanete.jpg",
      description:
        "Une quipe de scientifiques a dcouvert une exoplante qui pourrait soutenir la vie. Des tudes supplmentaires sont ncessaires pour comprendre son atmosphre.",
      size: "normal", // Taille normale
    },
    {
      title: "La mission Mars 2024 : des rvlations fascinantes  venir.",
      image: "/images/mars.jpg",
      description:
        "La mission Mars 2024 promet de rvler des informations indites sur la surface martienne et ses possibilits d'habitabilit.",
      size: "middle", // Taille moyenne
    },
    {
      title: "Les mystres des trous noirs expliqus par les experts.",
      image: "/images/trous-noir.jpg",
      description:
        "Des astrophysiciens partagent leurs recherches rcentes sur les trous noirs, rvlant des aspects fascinants de ces phnomnes clestes.",
      size: "large", // Taille grande
    },
    {
      title: "Une nouvelle mthode pour dtecter les ondes gravitationnelles.",
      image: "/images/gravitational-waves.jpg",
      description:
        "Une quipe de chercheurs a dvelopp une nouvelle mthode pour dtecter les ondes gravitationnelles, ouvrant la voie  de nouvelles dcouvertes.",
      size: "normal", // Taille normale
    },
    {
      title: "Exploration de l'intrieur de Jupiter.",
      image: "/images/jupiter.jpg",
      description:
        "Des missions spatiales rcentes ont permis de recueillir des donnes sur l'intrieur de Jupiter, rvlant des informations surprenantes.",
      size: "middle", // Taille moyenne
    },
    {
      title: "Les nouvelles avances dans l'astrophysique quantique.",
      image: "/images/quantum-astrophysics.jpg",
      description:
        "Des scientifiques explorent les nouvelles avances dans l'astrophysique quantique, ouvrant des horizons fascinants.",
      size: "large", // Taille grande
    },
  ];

  const [currentPage, setCurrentPage] = useState(1); // Page actuelle
  const [cardsPerPage, setCardsPerPage] = useState(3); // Nombre de cartes par page par dfaut (pour les grands crans)

  // Mettre  jour le nombre de cartes par page en fonction de la taille de l'cran
  useEffect(() => {
    const updateCardsPerPage = () => {
      if (window.innerWidth < 768) {
        setCardsPerPage(2); // 2 cartes sur les petits crans
      } else {
        setCardsPerPage(3); // 3 cartes sur les grands crans
      }
    };

    updateCardsPerPage(); // Excutez au chargement du composant
    window.addEventListener("resize", updateCardsPerPage); // Ajoutez un couteur d'vnements pour les redimensionnements

    return () => {
      window.removeEventListener("resize", updateCardsPerPage); // Nettoyez l'couteur lors du dmontage
    };
  }, []);

  // Calcul de l'index des cartes  afficher
  const indexOfLastCard = currentPage * cardsPerPage;
  const indexOfFirstCard = indexOfLastCard - cardsPerPage;
  const currentCards = newsData.slice(indexOfFirstCard, indexOfLastCard);

  // Fonction pour changer de page
  const paginate = (direction) => {
    if (direction === "next" && indexOfLastCard < newsData.length) {
      setCurrentPage(currentPage + 1);
    }
    if (direction === "prev" && currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  return (
    <section className="news-section">
      <h2>Actualits et dcouvertes</h2>
      <p>
        Restez inform des dernires nouvelles de l'astronomie et de la
        physique.
      </p>
      <div className="news-cards">
        {currentCards.map((news, index) => (
          <div className={`news-card ${news.size}`} key={index}>
            <img src={`${process.env.PUBLIC_URL}${news.image}`} alt={news.title} className="news-image" />
            <h3>{news.title}</h3>
            <p>{news.description}</p>
          </div>
        ))}
      </div>

      {/* Pagination Buttons */}
      <div className="pagination">
        <button
          onClick={() => paginate("prev")}
          disabled={currentPage === 1}
          className="prev-btn"
        >
          Prcdent
        </button>
        <button
          onClick={() => paginate("next")}
          disabled={indexOfLastCard >= newsData.length}
          className="next-btn"
        >
          Suivant
        </button>
      </div>
    </section>
  );
};

export default NewsSection;
