import React, { useState } from "react";
import "./Laws.css"; // Si vous souhaitez ajouter un style personnalis

const Laws = () => {
  const lawsData = [
    {
      title: "La gravit",
      description:
        "La gravit est la force d'attraction qui attire deux corps l'un vers l'autre, proportionnelle  leur masse. Cette force est responsable de la chute des objets et de l'orbite des plantes autour des toiles.",
      link: "/lois/gravity", // Lien vers une page ddie
      svg: (
        <svg width="50" height="50" viewBox="0 0 24 24">
          <path
            d="M12 0L10 3h4l-2-3zm2 3v10h-4V3h4zM10 13v10h4v-10h-4zm-4 10h4v-4H6v4zm0-6h4v-4H6v4zm-2-8v4h2V7H4z"
            fill="#000"
          />
        </svg>
      ), // Remplacez ceci par le SVG de gravit
    },
    {
      title: "La vitesse de la lumire",
      description:
        "La vitesse de la lumire dans le vide est d'environ 299 792 458 m/s. Elle reprsente la limite suprieure  laquelle l'information et la matire peuvent voyager, et elle joue un rle crucial dans la thorie de la relativit.",
      link: "/lois/speed-of-light",
      svg: (
        <svg width="50" height="50" viewBox="0 0 24 24">
          <path
            d="M12 3l2.5 2-2.5 2L12 3zm0 18l-2.5-2 2.5-2 2.5 2-2.5 2zm0-9l-6 6h12l-6-6z"
            fill="#000"
          />
        </svg>
      ), // Remplacez ceci par le SVG de vitesse
    },
    {
      title: "La loi de Newton",
      description:
        "Les trois lois de Newton dcrivent le mouvement des objets et les forces qui agissent sur eux. La premire loi stipule qu'un objet au repos reste au repos et un objet en mouvement continue  se dplacer  une vitesse constante,  moins qu'une force extrieure n'agisse sur lui.",
      link: "/lois/newtons-laws",
      svg: (
        <svg width="50" height="50" viewBox="0 0 24 24">
          <path d="M4 12h16M12 4l8 8-8 8-8-8z" fill="#000" />
        </svg>
      ), // Remplacez ceci par le SVG de Newton
    },
    {
      title: "L'effet Doppler",
      description:
        "L'effet Doppler dcrit le changement de frquence d'une onde en fonction de la vitesse de la source ou de l'observateur. Cela explique pourquoi le son d'une sirne semble plus aigu lorsque l'ambulance se rapproche et plus grave lorsqu'elle s'loigne.",
      link: "/lois/doppler-effect",
      svg: (
        <svg width="50" height="50" viewBox="0 0 24 24">
          <path d="M12 2L2 12h3v6h6v3h6v-3h3L12 2z" fill="#000" />
        </svg>
      ), // Remplacez ceci par le SVG de Doppler
    },
    {
      title: "La loi de la relativit",
      description:
        "Formule par Albert Einstein, la loi de la relativit gnrale et restreinte explique que le temps et l'espace ne sont pas absolus, mais relatifs  l'observateur. Cela a des implications profondes sur la comprhension du cosmos, y compris la dilatation du temps.",
      link: "/lois/relativity",
      svg: (
        <svg width="50" height="50" viewBox="0 0 24 24">
          <path
            d="M12 2a10 10 0 1010 10A10 10 0 0012 2zm1 15h-2v-2h2zm0-4h-2V7h2z"
            fill="#000"
          />
        </svg>
      ), // Remplacez ceci par le SVG de relativit
    },
    {
      title: "La thermodynamique",
      description:
        "Les lois de la thermodynamique dcrivent la relation entre la chaleur, le travail et l'nergie. La premire loi, par exemple, affirme que l'nergie totale d'un systme isol reste constante.",
      link: "/lois/thermodynamics",
      svg: (
        <svg width="50" height="50" viewBox="0 0 24 24">
          <path
            d="M3 3h18v2H3zm0 18h18v-2H3zm4-10h2v6H7zm8 0h2v6h-2z"
            fill="#000"
          />
        </svg>
      ), // Remplacez ceci par le SVG de thermodynamique
    },
    {
      title: "La loi de Coulomb",
      description:
        "La loi de Coulomb dcrit la force lectrostatique entre deux charges. Elle stipule que la force est proportionnelle au produit des charges et inversement proportionnelle au carr de la distance entre elles.",
      link: "/lois/coulombs-law",
      svg: (
        <svg width="50" height="50" viewBox="0 0 24 24">
          <path
            d="M12 2a10 10 0 00-7.07 17.07l4.9-4.9A3 3 0 1012 12a3 3 0 00-1.83 5.36L4.93 19.07A10 10 0 0012 2z"
            fill="#000"
          />
        </svg>
      ), // Remplacez ceci par le SVG de Coulomb
    },
    {
      title: "La conservation de l'nergie",
      description:
        "Cette loi stipule que l'nergie ne peut tre cre ni dtruite, seulement transforme d'une forme  une autre. C'est un principe fondamental qui s'applique  toutes les sciences physiques.",
      link: "/lois/conservation-of-energy",
      svg: (
        <svg width="50" height="50" viewBox="0 0 24 24">
          <path
            d="M12 2l-2 2h4zM2 12h4v2H2zm16 0h4v2h-4zM12 22l2-2h-4z"
            fill="#000"
          />
        </svg>
      ), // Remplacez ceci par le SVG de conservation
    },
    {
      title: "La loi de Hooke",
      description:
        "La loi de Hooke nonce que la force exerce sur un ressort est proportionnelle  son extension. Cette loi est essentielle dans l'tude des matriaux et de l'ingnierie.",
      link: "/lois/hookes-law",
      svg: (
        <svg width="50" height="50" viewBox="0 0 24 24">
          <path d="M4 12h16M12 4l8 8-8 8-8-8z" fill="#000" />
        </svg>
      ), // Remplacez ceci par le SVG de Hooke
    },
    {
      title: "Les lois de Kepler",
      description:
        "Les lois de Kepler dcrivent le mouvement des plantes autour du soleil. Elles incluent la loi des orbites, la loi des aires, et la loi des priodes, qui relient les distances des plantes au soleil  leurs priodes orbitales.",
      link: "/lois/keplers-laws",
      svg: (
        <svg width="50" height="50" viewBox="0 0 24 24">
          <path
            d="M12 2a10 10 0 10-10 10A10 10 0 0012 2zm0 14a4 4 0 11-4-4 4 4 0 014 4z"
            fill="#000"
          />
        </svg>
      ), // Remplacez ceci par le SVG de Kepler
    },
  ];

  const [expandedIndex, setExpandedIndex] = useState(null);

  const toggleDescription = (index) => {
    if (expandedIndex === index) {
      setExpandedIndex(null); // Replier si dj ouvert
    } else {
      setExpandedIndex(index); // Drouler la description
    }
  };

  return (
    <div className="laws-section">
      <h2>Lois Fondamentales de la Physique</h2>
      <p>
        Dcouvrez les lois qui rgissent notre univers, aussi bien sur Terre que
        dans l'espace.
      </p>
      <ul className="laws-list">
        {lawsData.map((law, index) => (
          <li key={index} className="law-item">
            <div className="law-icon">{law.svg}</div>{" "}
            {/* Affichage de l'icne SVG */}
            <h3>{law.title}</h3>
            <button
              onClick={() => toggleDescription(index)}
              className="learn-more-link"
            >
              {expandedIndex === index ? "Rduire" : "Lire plus"}
            </button>
            {expandedIndex === index && (
              <div className="law-description">
                <p>{law.description}</p>
                <a href={law.link} className="more-info-link">
                  En savoir plus
                </a>
              </div>
            )}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Laws;
