import React, { useState } from "react";
import "./News.css";

// Composant pour afficher un article de news
const NewsArticle = ({ article }) => {
  return (
    <div className="news-article">
      <img src={article.image} alt={article.title} className="article-image" />
      <h3>{article.title}</h3>
      <p>{article.description}</p>
      <a href={article.url} target="_blank" rel="noopener noreferrer">
        Lire plus
      </a>
    </div>
  );
};

// Composant principal pour la page News
const News = () => {
  // Tableau d'articles statiques
  const articles = [
    {
      title: "La dcouverte d'une nouvelle exoplante",
      description:
        "Des astronomes ont identifi une nouvelle exoplante dans la zone habitable de son toile.",
      url: "https://example.com/article1",
      image:
        "data:image/jpeg;base64,",
    },
    {
      title: "Les mystres des trous noirs",
      description:
        "Une tude rcente claire notre comprhension des trous noirs et de leur formation.",
      url: "https://example.com/article2",
      image:
        "data:image/jpeg;base64,",
    },
    {
      title: "Une avance dans la physique quantique",
      description:
        "Des physiciens ont fait une avance majeure dans la comprhension des particules subatomiques.",
      url: "https://example.com/article3",
      image:
        "data:image/jpeg;base64,",
    },
    {
      title: "Nouveaux dfis dans l'astrophysique",
      description:
        "Les chercheurs abordent de nouveaux dfis pour expliquer certaines anomalies dans l'univers.",
      url: "https://example.com/article4",
      image:
        "https://th.bing.com/th/id/OIP.1GNKgQykbPryflSmaRRqAQHaE8?w=248&h=180&c=7&r=0&o=5&dpr=1.5&pid=1.7",
    },
    {
      title: "La relativit gnrale explique simplement",
      description:
        "Une nouvelle vido ducative explique les concepts cls de la relativit gnrale d'Einstein.",
      url: "https://example.com/article5",
      image:
        "data:image/jpeg;base64,",
    },
    {
      title: "Les progrs de l'astrophysique dans les 10 dernires annes",
      description:
        "Un examen des dcouvertes majeures en astrophysique au cours de la dernire dcennie.",
      url: "https://example.com/article6",
      image:
        "https://th.bing.com/th/id/OIP.dHq808KYZB75wi40-VBregHaGH?w=186&h=180&c=7&r=0&o=5&dpr=1.5&pid=1.7",
    },
    {
      title: "L'nergie noire et l'expansion de l'univers",
      description:
        "Une recherche rcente jette une nouvelle lumire sur le rle de l'nergie noire dans l'expansion de l'univers.",
      url: "https://example.com/article7",
      image:
        "data:image/jpeg;base64,",
    },
    {
      title: "Le voyage vers Mars : quel avenir ?",
      description:
        "Des experts discutent des dfis et des opportunits lis  une mission humaine sur Mars.",
      url: "https://example.com/article8",
      image:
        "data:image/jpeg;base64,",
    },
    {
      title: "La physique des particules et le LHC",
      description:
        "Une mise  jour sur les expriences en cours au Grand collisionneur de hadrons.",
      url: "https://example.com/article9",
      image:
        "data:image/jpeg;base64,",
    },
    {
      title: "La dcouverte de l'onde gravitationnelle",
      description:
        "Un retour sur la dcouverte historique des ondes gravitationnelles et son impact sur la science.",
      url: "https://example.com/article10",
      image:
        "data:image/jpeg;base64,",
    },
  ];

  const articlesPerPage = 3; // Nombre d'articles  afficher par page
  const [currentPage, setCurrentPage] = useState(1); // tat pour suivre la page actuelle

  // Calculer les articles  afficher en fonction de la page actuelle
  const indexOfLastArticle = currentPage * articlesPerPage;
  const indexOfFirstArticle = indexOfLastArticle - articlesPerPage;
  const currentArticles = articles.slice(
    indexOfFirstArticle,
    indexOfLastArticle
  );

  // Calculer le nombre total de pages
  const totalPages = Math.ceil(articles.length / articlesPerPage);

  // Fonction pour changer de page
  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  return (
    <div className="news-container">
      <h2>Nouveauts en Physique et Astronomie</h2>
      {currentArticles.length > 0 ? (
        currentArticles.map((article, index) => (
          <NewsArticle key={index} article={article} />
        ))
      ) : (
        <p>Aucune nouvelle disponible pour le moment.</p>
      )}

      {/* Pagination */}
      <div className="pagination">
        {Array.from({ length: totalPages }, (_, index) => (
          <button
            key={index + 1}
            onClick={() => paginate(index + 1)}
            className={currentPage === index + 1 ? "active" : ""}
          >
            {index + 1}
          </button>
        ))}
      </div>
    </div>
  );
};

export default News;
